form {
  display: flex;
  flex-direction: column;
}

input {
  font-size: 1rem;
  padding: 0.5rem;
  margin: 0.5rem;
}

button {
  background: #007eb6;
  color: #fff;
  font-size: 1rem;
  padding: 0.5rem;
  width: 40%;
  margin-left: auto;
  margin-right: 0.5rem;
  border-radius: 4px;
}

h3 {
  margin: 0.5rem;
}
